"use client";

import React from "react";
import { useParams, useRouter } from "next/navigation";
import { Button } from "@/components/ui/Button";
import { Badge } from "@/components/ui/Badge";
import { Card, CardContent } from "@/components/ui/Card";
import { DataTable, type DataTableColumn } from "@/components/ui/DataTable";
import { ListPageShell } from "@/components/shells/ListPageShell";
import { apiFetch, ApiError } from "@/lib/api";

type ApiClient = {
  id: number;
  name: string;
  email: string | null;
  phone: string | null;
  company: string | null;
  tax_id: string | null;
  created_at: string;
  jobs_count: number;
};

type ApiMeta = {
  current_page: number;
  per_page: number;
  total: number;
  last_page: number;
};

export default function TenantClientsPage() {
  const router = useRouter();
  const params = useParams() as { tenant?: string; business?: string };
  const tenantSlug = params.business ?? params.tenant;

  const [loading, setLoading] = React.useState(true);
  const [error, setError] = React.useState<string | null>(null);
  const [clients, setClients] = React.useState<ApiClient[]>([]);
  const [q, setQ] = React.useState<string>("");

  const [meta, setMeta] = React.useState<ApiMeta | null>(null);

  const [pageIndex, setPageIndex] = React.useState(0);
  const [pageSize, setPageSize] = React.useState(10);

  React.useEffect(() => {
    let alive = true;

    async function load() {
      try {
        setLoading(true);
        setError(null);
        setMeta(null);

        if (typeof tenantSlug !== "string" || tenantSlug.length === 0) {
          throw new Error("Business is missing.");
        }

        const qs = new URLSearchParams();
        if (q.trim().length > 0) qs.set("query", q.trim());
        qs.set("page", String(pageIndex + 1));
        qs.set("per_page", String(pageSize));

        const res = await apiFetch<{ clients: ApiClient[]; meta?: ApiMeta }>(`/api/${tenantSlug}/app/clients?${qs.toString()}`);
        if (!alive) return;

        setClients(Array.isArray(res.clients) ? res.clients : []);
        setMeta(res.meta && typeof res.meta === "object" ? (res.meta as ApiMeta) : null);
      } catch (e) {
        if (!alive) return;

        if (e instanceof ApiError && e.status === 428 && typeof tenantSlug === "string" && tenantSlug.length > 0) {
          const next = `/app/${tenantSlug}/clients`;
          router.replace(`/app/${tenantSlug}/branches/select?next=${encodeURIComponent(next)}`);
          return;
        }

        setError(e instanceof Error ? e.message : "Failed to load clients.");
      } finally {
        if (!alive) return;
        setLoading(false);
      }
    }

    void load();

    return () => {
      alive = false;
    };
  }, [pageIndex, pageSize, q, router, tenantSlug]);

  const totalRows = typeof meta?.total === "number" ? meta.total : clients.length;
  const pageRows = clients;

  const columns = React.useMemo<Array<DataTableColumn<ApiClient>>>(
    () => [
      {
        id: "name",
        header: "Customer",
        cell: (row) => (
          <div className="min-w-0">
            <div className="truncate font-semibold text-[var(--rb-text)]">{row.name}</div>
          </div>
        ),
        className: "min-w-[240px]",
      },
      {
        id: "email",
        header: "Email",
        cell: (row) => <div className="text-sm text-zinc-700">{row.email ?? "—"}</div>,
        className: "min-w-[220px]",
      },
      {
        id: "phone",
        header: "Phone",
        cell: (row) => <div className="text-sm text-zinc-700">{row.phone ?? "—"}</div>,
        className: "min-w-[160px]",
      },
      {
        id: "jobs",
        header: "Jobs",
        cell: (row) => <Badge variant={(row.jobs_count ?? 0) > 0 ? "info" : "default"}>{row.jobs_count ?? 0}</Badge>,
        className: "whitespace-nowrap",
      },
      {
        id: "created",
        header: "Created",
        cell: (row) => <div className="text-sm text-zinc-600">{new Date(row.created_at).toLocaleDateString()}</div>,
        className: "whitespace-nowrap",
      },
      {
        id: "actions",
        header: "Actions",
        cell: (row) => (
          <div className="flex items-center justify-end gap-2">
            <Button
              variant="outline"
              size="sm"
              onClick={(e) => {
                e.stopPropagation();
                if (typeof tenantSlug !== "string" || tenantSlug.length === 0) return;
                router.push(`/app/${tenantSlug}/clients/${row.id}`);
              }}
            >
              View
            </Button>
            <Button
              variant="outline"
              size="sm"
              onClick={(e) => {
                e.stopPropagation();
                if (typeof tenantSlug !== "string" || tenantSlug.length === 0) return;
                router.push(`/app/${tenantSlug}/clients/${row.id}/edit`);
              }}
            >
              Edit
            </Button>
          </div>
        ),
        className: "whitespace-nowrap text-right",
        headerClassName: "text-right",
      },
    ],
    [router, tenantSlug],
  );

  return (
    <ListPageShell
      title="Customers"
      description="Your customer list and contact details."
      actions={
        <div className="flex items-center gap-2">
          <Button
            variant="outline"
            size="sm"
            onClick={() => {
              router.back();
            }}
          >
            Back
          </Button>
          <Button
            variant="primary"
            size="sm"
            onClick={() => {
              if (typeof tenantSlug !== "string" || tenantSlug.length === 0) return;
              router.push(`/app/${tenantSlug}/clients/new`);
            }}
          >
            New customer
          </Button>
        </div>
      }
      error={error}
    >
      <Card className="shadow-none">
        <CardContent className="pt-5">
          <DataTable
            // title={typeof tenantSlug === "string" ? `Customers` : "Customers"}
            data={pageRows}
            loading={loading}
            emptyMessage="No customers."
            columns={columns}
            getRowId={(row) => row.id}
            search={{ placeholder: "Search name, email, phone, or ID..." }}
            server={{
              query: q,
              onQueryChange: (value) => {
                setQ(value);
                setPageIndex(0);
              },
              pageIndex,
              onPageIndexChange: setPageIndex,
              pageSize,
              onPageSizeChange: (value) => {
                setPageSize(value);
                setPageIndex(0);
              },
              totalRows,
            }}
            onRowClick={(row) => {
              if (typeof tenantSlug !== "string" || tenantSlug.length === 0) return;
              router.push(`/app/${tenantSlug}/clients/${row.id}`);
            }}
          />
        </CardContent>
      </Card>
    </ListPageShell>
  );
}
